<template>
  <div>
    <!--    <h2 class="title">分步测试</h2>-->
    <div class="container">
      <div class="button-columns">
        <div v-for="(column, index) in buttonColumns" :key="index" class="button-column">
          <el-button v-for="btn in column" :key="btn.id" type="primary" @click="openDialog(btn.id, btn.name)">
            {{ btn.name }}
          </el-button>
        </div>
      </div>
    </div>
    <!-- 重置和紧急停止按钮 -->
    <div class="fixed-buttons">
      <el-button type="success" class="reset-button" @click="resetSystem">复位</el-button>
      <el-button type="danger" class="emergency-button" @click="emergencyStop">急停</el-button>
    </div>

    <!-- 弹窗-->
    <el-dialog title="输入参数" :visible="dialogVisible" :before-close="handleClose">
      <el-input v-model="parameter" placeholder="请输入参数"></el-input>
      <template #footer>
        <el-button @click="dialogVisible = false">取消</el-button>
        <el-button type="primary" @click="submitParameter">确定</el-button>
      </template>
    </el-dialog>
  </div>
</template>

<script>
import axios from 'axios';

const baseURL = 'http://127.0.0.1:8080/buttonAction';

export default {
  name: 'ButtonsPage',
  data() {
    return {
      buttons: [
        {id: 1, name: '机器人重置'},
        {id: 2, name: '机器人取碗'},
        {id: 3, name: '机器人出汤'},
        {id: 4, name: '机器人取粉丝'},
        {id: 5, name: '取餐口复位'},
        {id: 6, name: '取餐口出餐'},
        {id: 7, name: '调料机测试（配方）'},
        {id: 8, name: '抽汤泵（秒）'},
        {id: 9, name: '转台复位'},
        {id: 10, name: '转台下一工位'},
        {id: 11, name: '碗复位'},
        {id: 12, name: '蒸汽测试（秒）'},
        {id: 13, name: '蒸汽打开'},
        {id: 14, name: '蒸汽关闭'},
        {id: 15, name: '震动器测试'},
        {id: 16, name: '待办功能'}
      ],
      buttonsPerColumn: 8, // 每列显示的按钮数量
      buttonColumns: [], // 存储分组后的按钮数组
      dialogVisible: false, // 控制弹窗显示与隐藏
      parameter: '', // 存储输入的参数
      currentButtonId: null, // 存储当前点击的按钮ID
      currentButtonName: '' // 存储当前点击的按钮名称
    };
  },
  created() {
    this.buttonColumns = this.chunkArray(this.buttons, this.buttonsPerColumn);
  },
  methods: {
    async sendRequest(url, data = {}) {
      try {
        const response = await axios.post(url, data, {
          headers: {
            'Content-Type': 'application/json'
          }
        });
        this.$message.success(`操作成功：${response.data}`);
        console.log(response.data);
      } catch (error) {
        this.$message.error('操作失败');
        console.error(error);
      }
    },
    openDialog(id, name) {
      if(id===7 || id===8 ||id===12){
        this.dialogVisible = true;
      }else {
        this.dialogVisible = false;
      }
      this.currentButtonId = id;
      this.currentButtonName = name;
    },
    handleClose() {
      this.dialogVisible = false;
    },
    submitParameter() {
      const url = `${baseURL}/${this.currentButtonId}`;
      const data = { parameter: this.parameter };
      this.sendRequest(url, data);
      this.dialogVisible = false;
      this.parameter = '';
    },
    emergencyStop() {
      this.sendRequest(`${baseURL}/emergencyStop`);
    },
    resetSystem() {
      this.sendRequest(`${baseURL}/reset`);
    },
    // 将数组分成指定大小的块
    chunkArray(arr, size) {
      return Array.from({ length: Math.ceil(arr.length / size) }, (_, index) =>
          arr.slice(index * size, index * size + size)
      );
    }
  }
};
</script>

<style scoped>
.title {
  text-align: center;
  margin-top: 20px;
  font-size: 24px;
}

.container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: calc(100vh - 100px); /* 调整容器高度，减去标题和急停按钮的高度 */
  padding: 0 20px; /* 可根据需要调整 */
  box-sizing: border-box;
}

.button-columns {
  display: flex;
  justify-content: center;
  width: 100%;
  max-width: 1200px; /* 根据需要调整 */
}

.button-column {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0 10px; /* 调整列与列之间的间距 */
}

.el-button {
  margin: 5px 0; /* 调整按钮之间的间距 */
}

.fixed-buttons {
  position: fixed;
  bottom: 20px;
  right: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.reset-button {
  background-color: green; /* 设置背景绿色 */
  color: white; /* 设置文字颜色为白色 */
  border: none; /* 去除边框 */
  margin-bottom: 10px; /* 设置复位按钮和急停按钮之间的间距 */
}

.emergency-button {
  background-color: red; /* 设置背景红色 */
  color: white; /* 设置文字颜色为白色 */
  border: none; /* 去除边框 */
}
</style>
